// src/app/ranking/page.tsx
"use client";

import { useEffect, useMemo, useRef, useState } from "react";
import { motion } from "framer-motion";
import AppShell from "@/components/shell/AppShell";
import { supabaseBrowser } from "@/lib/supabase/client";

/* =========================
   Tipos do endpoint
========================= */
type RankItem = {
  user_id: string;
  name?: string | null;
  email?: string | null;
  points: number;
  total: number; // alias
  last_completed_at?: string | null; // se sua API enviar
  trend?: number; // se sua API enviar
};
type ApiResp = {
  ok: boolean;
  leaderboard: RankItem[];
  page: number;
  pageSize: number;
  total: number;
  pages: number;
  updatedAt?: string;
};

/* =========================
   Utils visuais
========================= */
function Avatar({ name }: { name?: string | null }) {
  const initials = (name || "??")
    .split(" ")
    .filter(Boolean)
    .slice(0, 2)
    .map((s) => s[0]?.toUpperCase())
    .join("");
  return (
    <div className="h-10 w-10 rounded-full bg-white/10 border border-white/10 flex items-center justify-center text-sm">
      {initials || "?"}
    </div>
  );
}

function RankSkeletonRow() {
  return (
    <li className="grid grid-cols-[90px_1fr_160px_140px_180px] gap-3 px-6 py-4 animate-pulse">
      <div className="h-3 w-8 bg-white/10 rounded" />
      <div className="flex items-center gap-3">
        <div className="h-10 w-10 rounded-full bg-white/10" />
        <div className="h-3 w-48 bg-white/10 rounded" />
      </div>
      <div className="h-3 w-16 bg-white/10 rounded" />
      <div className="h-3 w-10 bg-white/10 rounded" />
      <div className="h-3 w-24 bg-white/10 rounded" />
    </li>
  );
}

function RankEmpty() {
  return (
    <div className="px-6 py-12 text-center text-white/60">
      Ainda não há participantes no ranking.
    </div>
  );
}

function RankRow({
  me,
  i,
  name,
  points,
  lastActive,
  trend = 0,
}: {
  me?: boolean;
  i: number;
  name?: string | null;
  points: number;
  lastActive?: string | null;
  trend?: number;
}) {
  const arrow = trend === 0 ? "—" : trend > 0 ? "↑" : "↓";
  return (
    <li
      className={`grid grid-cols-[90px_1fr_160px_140px_180px] items-center gap-3 px-6 py-4
                  hover:bg-white/[0.04] transition-colors
                  ${me ? "bg-lime-400/5 ring-1 ring-lime-300/20" : ""}`}
    >
      <div className="text-sm text-white/60">#{i}</div>
      <div className="flex items-center gap-3">
        <Avatar name={name} />
        <div className="font-medium text-white/90 line-clamp-1">{name || "Sem nome"}</div>
      </div>
      <div className="text-white/80">{points} pts</div>
      <div
        className={`text-sm ${
          trend > 0 ? "text-lime-300" : trend < 0 ? "text-rose-300" : "text-white/50"
        }`}
      >
        {arrow} {trend !== 0 ? Math.abs(trend) : ""}
      </div>
      <div className="text-sm text-white/60">
        {lastActive ? new Date(lastActive).toLocaleDateString("pt-BR") : "—"}
      </div>
    </li>
  );
}

/* =========================
   Pódio Top-3
========================= */
function Podium({ items }: { items: RankItem[] }) {
  const [first, second, third] = items;

  const Medal = ({ i }: { i: number }) => (
    <span
      className={`text-xs px-2 py-0.5 rounded-md border
      ${
        i === 1
          ? "bg-yellow-400/15 border-yellow-300/30 text-yellow-200"
          : i === 2
          ? "bg-slate-300/15 border-slate-200/30 text-slate-200"
          : "bg-amber-700/15 border-amber-700/30 text-amber-300"
      }`}
    >
      {i === 1 ? "🥇 1º" : i === 2 ? "🥈 2º" : "🥉 3º"}
    </span>
  );

  if (!first) return null;

  return (
    <div className="grid grid-cols-3 gap-4 mt-6">
      {/* 2º */}
      <div className="rounded-2xl p-4 bg-white/[0.03] border border-white/10 flex flex-col items-center justify-center">
        {second ? (
          <>
            <Medal i={2} />
            <div className="mt-3">
              <Avatar name={second.name} />
            </div>
            <div className="mt-2 text-sm text-white/80 text-center line-clamp-1">{second.name}</div>
            <div className="text-xs text-white/60">{second.points} pts</div>
          </>
        ) : (
          <div className="h-20" />
        )}
      </div>

      {/* 1º */}
      <div className="rounded-2xl p-5 bg-gradient-to-b from-neutral-900 to-black border border-white/10 flex flex-col items-center justify-center shadow-[0_0_20px_rgba(163,230,53,0.08)]">
        <Medal i={1} />
        <div className="mt-3 scale-110">
          <Avatar name={first.name} />
        </div>
        <div className="mt-2 text-white text-center font-medium line-clamp-1">{first.name}</div>
        <div className="text-sm text-lime-300">{first.points} pts</div>
      </div>

      {/* 3º */}
      <div className="rounded-2xl p-4 bg-white/[0.03] border border-white/10 flex flex-col items-center justify-center">
        {third ? (
          <>
            <Medal i={3} />
            <div className="mt-3">
              <Avatar name={third.name} />
            </div>
            <div className="mt-2 text-sm text-white/80 text-center line-clamp-1">{third.name}</div>
            <div className="text-xs text-white/60">{third.points} pts</div>
          </>
        ) : (
          <div className="h-20" />
        )}
      </div>
    </div>
  );
}

/* =========================
   Chat em tempo real (Supabase Realtime)
========================= */
function ChatPanel() {
  const supabase = useMemo(() => supabaseBrowser(), []);
  const [connected, setConnected] = useState(false);
  const [participants, setParticipants] = useState(0);
  const [messages, setMessages] = useState<{ id: string; user?: string; text: string }[]>([]);
  const [text, setText] = useState("");
  const listRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const channel = supabase.channel("ranking-global", {
      config: { broadcast: { self: true }, presence: { key: Math.random().toString(36).slice(2) } },
    });

    channel
      .on("presence", { event: "sync" }, () => {
        const state = channel.presenceState();
        const count = Object.values(state).reduce((acc, arr: any) => acc + (arr as any[]).length, 0);
        setParticipants(count);
        setConnected(true);
      })
      .on("broadcast", { event: "message" }, (payload) => {
        const m = payload.payload as { id: string; user?: string; text: string };
        setMessages((s) => [...s, m]);
      })
      .subscribe(async (status) => {
        if (status === "SUBSCRIBED") {
          await channel.track({ online_at: new Date().toISOString() });
        }
      });

    return () => {
      supabase.removeChannel(channel);
    };
  }, [supabase]);

  useEffect(() => {
    listRef.current?.scrollTo({ top: 999999, behavior: "smooth" });
  }, [messages]);

  async function sendMessage() {
    const t = text.trim();
    if (!t) return;
    const m = { id: crypto.randomUUID(), text: t };
    await supabase.channel("ranking-global").send({
      type: "broadcast",
      event: "message",
      payload: m,
    });
    setText("");
  }

  return (
    <div className="rounded-2xl overflow-hidden border border-white/10 bg-white/[0.03] flex flex-col h-full">
      <div className="px-4 py-3 border-b border-white/10 text-white/80 text-sm flex items-center justify-between">
        <div>
          <div className="font-medium">Chat do Ranking</div>
          <div className="text-xs text-white/60">
            {connected ? (
              <>
                <span className="inline-flex h-2 w-2 rounded-full bg-lime-400 mr-1.5 align-middle" />
                Conectado a <span className="font-mono">ranking-global</span>
              </>
            ) : (
              "Conectando…"
            )}
          </div>
        </div>
        <div className="text-xs px-2 py-1 rounded-md bg-white/10 border border-white/10">
          {participants} participantes
        </div>
      </div>

      <div ref={listRef} className="flex-1 overflow-auto px-4 py-3 space-y-3">
        {messages.length === 0 ? (
          <div className="text-sm text-white/50">Sem mensagens ainda.</div>
        ) : (
          messages.map((m) => (
            <div key={m.id} className="flex items-start gap-2">
              <div className="h-7 w-7 rounded-full bg-white/10 border border-white/10 flex items-center justify-center text-[11px]">
                U
              </div>
              <div className="max-w-[80%] rounded-xl bg-white/10 border border-white/10 px-3 py-2 text-sm">
                {m.text}
              </div>
            </div>
          ))
        )}
      </div>

      <form
        onSubmit={(e) => {
          e.preventDefault();
          sendMessage();
        }}
        className="p-3 border-t border-white/10 flex items-center gap-2"
      >
        <input
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Escreva uma mensagem…"
          className="flex-1 bg-black/40 border border-white/10 rounded-full px-3 py-2 text-sm outline-none focus:border-white/20"
        />
        <button
          type="submit"
          className="text-sm px-3 py-2 rounded-full bg-white/10 border border-white/10 hover:bg-white/15"
        >
          Enviar
        </button>
      </form>
    </div>
  );
}

/* =========================
   Página principal
========================= */
export default function RankingPage() {
  const [loading, setLoading] = useState(true);
  const [items, setItems] = useState<RankItem[]>([]);
  const [page, setPage] = useState(1);
  const [pages, setPages] = useState(1);
  const [participants, setParticipants] = useState(0);
  const [updatedAt, setUpdatedAt] = useState<string | undefined>(undefined);

  const currentUserIdRef = useRef<string | null>(null); // se quiser destacar "eu"

  async function fetchRanking(p = page) {
    setLoading(true);
    try {
      const res = await fetch(`/api/ranking?page=${p}&pageSize=25`, { cache: "no-store" });
      const json: ApiResp = await res.json();
      if (json.ok) {
        setItems(json.leaderboard);
        setPages(json.pages);
        setParticipants(json.total);
        setUpdatedAt(json.updatedAt);
      } else {
        setItems([]);
      }
    } catch {
      setItems([]);
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    fetchRanking(page);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page]);

  const top3 = items.slice(0, 3);

  return (
    <AppShell>
      <div className="grid grid-cols-1 lg:grid-cols-[1.6fr_1fr] gap-6 px-5 md:px-8 py-8">
        {/* ======= Coluna esquerda: Ranking ======= */}
        <div>
          {/* Hero */}
          <motion.div
            initial={{ opacity: 0, y: 8 }}
            animate={{ opacity: 1, y: 0 }}
            className="relative overflow-hidden rounded-3xl p-6 md:p-8
                       bg-gradient-to-b from-neutral-900 to-black
                       shadow-[inset_0_0_0_1px_rgba(255,255,255,0.05)]"
          >
            <div className="flex items-start justify-between gap-4 flex-wrap">
              <div>
                <h1 className="text-2xl md:text-3xl font-semibold text-white/90">Ranking Geral</h1>
                <p className="text-white/60 mt-1">Veja quem está subindo no topo</p>
                <div className="mt-3 flex flex-wrap items-center gap-2">
                  <span className="text-[11px] px-2 py-1 rounded-md bg-white/10 border border-white/10">
                    <span className="inline-flex h-2 w-2 rounded-full bg-lime-400 mr-1.5 animate-pulse" />
                    {updatedAt ? "Atualizado agora" : "Atualizando…"}
                  </span>
                  <span className="text-[11px] px-2 py-1 rounded-md bg-white/10 border border-white/10">
                    {participants} participantes
                  </span>
                  <button
                    onClick={() => fetchRanking(1)}
                    className="text-[11px] px-2 py-1 rounded-md bg-white/10 border border-white/10 hover:bg-white/15"
                  >
                    Atualizar
                  </button>
                </div>
              </div>
              {/* Filtros simples (placeholders) */}
              <div className="flex items-center gap-2">
                <select className="bg-white/10 border border-white/10 text-sm rounded-lg px-2 py-1.5">
                  <option>All-time</option>
                  <option>Semana</option>
                </select>
                <select className="bg-white/10 border border-white/10 text-sm rounded-lg px-2 py-1.5">
                  <option>Global</option>
                  <option>Meu curso</option>
                </select>
              </div>
            </div>

            {/* Pódio */}
            {!loading && top3.length > 0 && <Podium items={top3} />}
          </motion.div>

          {/* Lista/Tabela */}
          <div className="mt-6 rounded-2xl overflow-hidden bg-white/[0.03] border border-white/10">
            <div className="grid grid-cols-[90px_1fr_160px_140px_180px] px-6 py-3 text-xs uppercase tracking-wide text-white/60 border-b border-white/10 sticky top-0 bg-black/50 backdrop-blur">
              <div>Posição</div>
              <div>Usuário</div>
              <div>Pontos</div>
              <div>Variação</div>
              <div>Última atividade</div>
            </div>

            {loading ? (
              <ul className="divide-y divide-white/10">
                {Array.from({ length: 6 }).map((_, i) => (
                  <RankSkeletonRow key={i} />
                ))}
              </ul>
            ) : items.length === 0 ? (
              <RankEmpty />
            ) : (
              <ul className="divide-y divide-white/10">
                {items.map((u, idx) => (
                  <RankRow
                    key={u.user_id}
                    i={idx + 1 + (page - 1) * 25}
                    name={u.name}
                    points={u.points}
                    lastActive={u.last_completed_at}
                    trend={u.trend ?? 0}
                    me={currentUserIdRef.current === u.user_id}
                  />
                ))}
              </ul>
            )}

            {/* Paginação */}
            <div className="flex items-center justify-between px-6 py-3 border-t border-white/10">
              <button
                disabled={page === 1}
                onClick={() => setPage((p) => Math.max(1, p - 1))}
                className="text-sm px-3 py-1.5 rounded-md bg-white/10 border border-white/10 disabled:opacity-40"
              >
                Anterior
              </button>
              <div className="text-xs text-white/60">
                Página {page} de {pages}
              </div>
              <button
                disabled={page >= pages}
                onClick={() => setPage((p) => Math.min(pages, p + 1))}
                className="text-sm px-3 py-1.5 rounded-md bg-white/10 border border-white/10 disabled:opacity-40"
              >
                Próxima
              </button>
            </div>
          </div>
        </div>

        {/* ======= Coluna direita: Chat ======= */}
        <div className="min-h-[520px]">
          <ChatPanel />
        </div>
      </div>
    </AppShell>
  );
}
